<script setup lang="ts">
import { THEME_COLOR } from "~/constants";

export interface IconSportTypeProps {
  src?: string;
  fallbackSrc?: string;
  width?: number;
  height?: number;
  backgroundWidth?: string;
  backgroundHeight?: string;
  isActive?: boolean;
  tagType?: string;
  showPlay?: boolean;
}
const color = useColorMode();
const props = withDefaults(defineProps<IconSportTypeProps>(), {
  fallbackSrc: "/images/icons/tags/general/general_good.svg",
  width: 16,
  height: 16,
  isActive: false,
});
const disabledBackground = computed(() => {
  return !(props.tagType === "soccer_redcard"
    || props.tagType === "soccer_yellowcard"
    || props.tagType === "futsal_redcard"
    || props.tagType === "futsal_yellowcard"
    || props.tagType === "handball_yellow_card"
    || props.tagType === "handball_red_card");
});
const isDark = computed(() => color.value === THEME_COLOR.DARK);
const isActive = computed(() => props.isActive && disabledBackground.value);
const iconTagBackground = computed(() => {
  if (isDark.value)
    return isActive.value ? "var(--base-primary-color)" : "var(--base-card-bgSecondaryColor)";
  else
    return isActive.value ? "var(--base-primary-color)" : "none";
});
const iconTagBorder = computed(() => (!isDark.value && !isActive.value) ? "0.5px solid #d9d9d9" : "none");
</script>

<template>
  <div
    class="iconTagType"
    :style="{
      border: iconTagBorder,
      background: iconTagBackground,
      width: props.backgroundHeight,
      height: props.backgroundWidth,
    }"
  >
    <svg width="0" height="0">
      <filter id="colorize-filter">
        <feColorMatrix
          type="matrix"
          values="
            0 0 0 0 0.17
            0 0 0 0 0.22
            0 0 0 0 0.29
            0 0 0 1 0"
        />
      </filter>
    </svg>
    <IconPlay v-if="showPlay" :fill="isDark ? '#FFF' : '#8F959F'" />
    <BaseImage
      v-else
      alt="tag icon"
      :src="props.src"
      :width="props.width"
      :height="props.height"
      :fallback-src="fallbackSrc"
      :class="{ 'colorized-svg': !isActive && !isDark }"
    />
  </div>
</template>

<style lang="scss">
.iconTagType {
  @apply h-9 w-9 rounded-full flex items-center cursor-pointer justify-center;
}

.colorized-svg {
  filter: url(#colorize-filter);
}
</style>
